export const generateLink = (link: string) => {
  const linkRemoved = removeAccents(link.toLowerCase());
   const newLink = linkRemoved.replace(/[^a-z0-9 -]/g, '') // remove invalid chars
    .replace(/\s+/g, '-') // collapse whitespace and replace by -
    .replace(/-+/g, '-'); // collapse dashes
  return newLink;
};
